html, body, div, h1, h2, h3, h4, h5, h6, p {
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

* {
  box-sizing: border-box;
}

body {
  width: 100%;
  font-family: Roboto, sans-serif;
}

header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.moyo-logo-link {
  align-items: center;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
}

.moyo-logo {
  transition: transform .3s;
}

@media (width >= 1024px) {
  .moyo-logo:hover {
    transition: transform .3s;
    transform: scale(1.1);
  }
}

.nav__list {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s linear;
  display: flex;
}

@media (width >= 1024px) {
  .nav__link:hover {
    color: #00acdc;
  }
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.stars {
  margin: 0;
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.card {
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s ease-in-out;
}

@media (width >= 1024px) {
  .card:hover {
    transform: scale(1.2);
  }
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  color: #060b35;
  margin: 0;
  font-family: inherit;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s linear;
}

@media (width >= 1024px) {
  .card:hover .card__title {
    color: #34568b;
    transition: color .3s linear;
  }
}

.card__product-code {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__product-code--spaced {
  margin: 4px 0 16px;
}

.card__reviews-container {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__reviews-count {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price-container {
  justify-content: space-between;
  margin-block: 24px 16px;
  display: flex;
}

.card__price-label {
  color: #616070;
  width: 31px;
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  width: 50px;
  margin: 0;
  font-family: inherit;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: border, background-color, color, all .3s linear;
  display: flex;
}

@media (width >= 1024px) {
  .card__buy-button:hover {
    box-sizing: border-box;
    color: #00acdc;
    background-color: #0000;
    border: 1px solid #00acdc;
    transition: border, background-color, color, all .3s linear;
  }
}

.catalog {
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, 200px);
  }
}

